<template>
  <div class="products">
    <h1>Products</h1>
    <div id="div1">
    <button @click="resetProducts" :disabled="prodButtonEnabled" > Reset </button>
    <br>
    <button @click="getProducts" :disabled="!prodButtonEnabled" > Get Products </button>
    <br>
    </div>
    <table align='center'  >
      <tr v-for="(product, index) in products" :key="product.id"> 
        <td>
          {{product.name}} 
        </td>
        <td>
          ${{product.price}} 
        </td>
        <td>
         <button @click="addToGroup(1, index)" :disabled="product.inGroup"> Group 1 </button> 
         <button @click="addToGroup(2, index)" :disabled="product.inGroup"> Group 2 </button> 
        </td>
      </tr>
    </table>
    <div>
          <Group :groupType="1"> </Group>
          <Group :groupType="2"> </Group>
    </div>

  </div>
</template>

<script>
import store from '../store/store';
import Group from './Group';
export default {
  name: 'Products',
  data(){
    return {
      prodButtonEnabled: true
    }
  },
  props: {
  },
  computed: {
    products(){
      return store.state.products;
    }
  },

  methods: {
    getProducts(){
      this.prodButtonEnabled = false;
      store.dispatch('getProductsFromApi');
    },
    resetProducts(){
      this.prodButtonEnabled = true;
      store.dispatch('resetProducts');
    },
    addToGroup(type, index){
      store.dispatch('addToGroup', {type, index});
    }
  },
  components: {
    Group
  }
}
</script>

