<template>
  <div class="group">
    <h1>Group {{groupType}}</h1>

    <table style="width:100%">
      <tr v-for="(product, index) in group" :key="product.id"> 
        <td>
          {{product.name}} 
        </td>
        <td>
          <button @click="removeFromGroup(groupType, index, product.id)"> x </button> 
        </td>

      </tr>
    </table>
  </div>
</template>

<script>
import store from "../store/store";
export default {
  name: "Group",
  props: {
    groupType: {
      type: Number,
      required: true
    }
  },

  methods: {
    removeFromGroup(type, index, id){
      store.dispatch('removeFromGroup', {type, index, id});
      //console.log(index);
    }
  },

  computed: {
    group() {
      if (this.groupType === 1) {
        return store.state.group1;
      } else if (this.groupType === 2) {
        return store.state.group2;
      }
    }
  }
};
</script>

