
import api from '../apis/api'

export default {
    getProductsFromApi(context){
      var prods = api.getProducts();
      prods.forEach(
        (prod) => {
          prod.inGroup = false;
        }
      );
      context.commit('setProducts', prods);
    },

    addToGroup(context, data){
      context.commit('pushProdToGroup', data);
      context.commit('setInGroup', data.index);
    },

    removeFromGroup(context, data){
      context.commit('pullProdFromGroup', data);
      context.commit('setOutOfGroup', data.id);
    },

    resetProducts(context){
      context.commit('reset');
    }
 }