
export default {
    setProducts(state, products) {
      state.products = products;
    },

    pushProdToGroup(state, data){
      if(data.type === 1){
        state.group1.push(state.products[data.index]);
      }
      else if(data.type === 2) {
        state.group2.push(state.products[data.index]);
      }
    },

    pullProdFromGroup(state, data){
      if(data.type == 1){
        state.group1.splice(data.index, 1);
      } else if (data.type === 2) {
        state.group2.splice(data.index, 1);
      }
    },

    setInGroup(state, index){
      state.products[index].inGroup = true;
    },

    setOutOfGroup(state, id){
      var index = state.products.findIndex(
        (product) => {
          return product.id === id;
        }
      );
      state.products[index].inGroup = false;
    },

    reset(state){
      state.products = [];
      state.group1 = [];
      state.group2 = [];
    }
}